import { useAppWindowRender } from 'utils/zustand/useAppWindowRender'
import AppWindow from '../AppWindow'
import BgIcon from 'components/bgIcon/BgIcon'
import fileIcon from 'asset/images/fileIcon.png'

export default function TrashCanWindow() {
  const { onClickWindowClose } = useAppWindowRender()

  return (
    <AppWindow width="500px" top="10%" left="30%">
      <AppWindow.Header onClick={() => onClickWindowClose('trashCanWindow')}></AppWindow.Header>
      <AppWindow.Content>
        <AppWindow.ContentSection>
          <BgIcon title="" imgObj={{ src: fileIcon, alt: '' }} border="1px solid rgb(198, 198, 198)" color="black" />
        </AppWindow.ContentSection>
      </AppWindow.Content>
    </AppWindow>
  )
}
