import { ThemeProvider } from 'styled-components'
import original from 'react95/dist/themes/original'
import Layout from './components/layout/Layout'
import BgIcon from 'components/bgIcon/BgIcon'
import myComputer from 'asset/images/myComputer.png'
import trashIcon from 'asset/images/trash.png'
import networkIcon from 'asset/images/network.png'
import { useAppWindowRender } from 'utils/zustand/useAppWindowRender'
import MyProjectsWindow from 'components/AppWindow/windows/MyProjectsWindow'
import MyInfoWindow from 'components/AppWindow/windows/MyInfoWindow'
import TrashCanWindow from 'components/AppWindow/windows/TrashCanWindow'
import notePadIcon from 'asset/images/notepad.png'
import AlertWindow from 'components/AppWindow/windows/AlertWindow'

export default function App() {
  const { isOpen, onClickWindowOpen } = useAppWindowRender()
  return (
    <ThemeProvider theme={original}>
      <Layout>
        <Layout.WindowContainer>
          <BgIcon title=" " imgObj={{ src: myComputer, alt: 'myComputer' }} onDoubleClick={() => onClickWindowOpen('myInfoWindow')} border="1px solid rgb(0, 128, 128)" color="white" />
          <BgIcon title=" " imgObj={{ src: networkIcon, alt: 'networkIcon' }} onDoubleClick={() => onClickWindowOpen('myProjectWindow')} border="1px solid rgb(0, 128, 128)" color="white" />
          <BgIcon title="" imgObj={{ src: notePadIcon, alt: 'trashicon' }} onDoubleClick={() => onClickWindowOpen('guestBookWindow')} border="1px solid rgb(0, 128, 128)" color="white" />
          <BgIcon title="" imgObj={{ src: trashIcon, alt: 'trashicon' }} onDoubleClick={() => onClickWindowOpen('trashCanWindow')} border="1px solid rgb(0, 128, 128)" color="white" />
          {isOpen.myProjectWindow && <MyProjectsWindow />}
          {isOpen.myInfoWindow && <MyInfoWindow />}
          {isOpen.trashCanWindow && <TrashCanWindow />}
          {isOpen.guestBookWindow && <AlertWindow text="   ..." />}
        </Layout.WindowContainer>
        <Layout.TaskBar />
        {/*         */}
      </Layout>
    </ThemeProvider>
  )
}
